body{
  background-color: rgb(155, 185, 240);
}
#nav{
  position: fixed;
  top: 0px;
  width:100%;
  background-color: rgb(64, 94, 150);
}
.links{
  display:inline-block;
  text-align: center;
  padding: 10px;
  color:black;
  text-decoration: none;
  font-size: 17px;
}
.links:hover{
  background-color: rgb(48, 44, 85);
}
.selected{
  color:aliceblue;
}
.rubrik{
  color: black;
  font-family: 'Pacifico';
  font-size: 200%;
  padding-right: 10%;
}
#rektangel{
  border:rgb(64, 94, 150);
  padding: 10vw;
  padding-top: 20%;
  background-color: rgb(64, 94, 150);
  margin-left: 20%;
  margin-right: 20%;
  margin-bottom: auto;
  margin-top: auto;
  color: black;
  font-family: 'Lucida Handwriting';
  font-size: 100%;
}
.rektangel3{
  border:rgb(64, 94, 150);
  padding: 20vw;
  padding-top: 20%;
  background-color: rgb(64, 94, 150);
  margin-left: 10%;
  margin-right: 10%;
  margin-bottom: auto;
  margin-top: auto;
  color: black;
  font-family: 'Lucida Handwriting';
  font-size: 150%;
}
#påskägg1{
  border: none;
  background: none;
  cursor: pointer;
  font-family: 'Lucida Handwriting';
}
.profilbild{
  float:right;
  width: 45%;
  height: auto;
  margin-top: 2%;
  padding: 1em;
  border: 10px black;
  position: static;
}
.gissanumretfoto{

  width:55%;
  height: auto;
  margin-top: -2%;
  padding: 1vw;
  border: 10px black;
  position: static;
}
.loppetfoto{
  width: 50%;
  height: auto;
  border: 10px black;
}
.box{
  width:40%;
  margin: 0 auto;
  background: rgba(255,255,255,0.2);
  padding: 35px;
  border: 2px solid white;
  border-radius: 20px/50px;
  background-clip: padding-box;
  text-align: center;
}
.button{
  font-size: 70%;
  padding: 10px;
  color: white;
  border: 2px solid rgb(155, 185, 240);
  border-radius: 20px/50px;
  text-decoration: none;

}
.button:hover{
  background: rgb(155, 185, 240);
}
.overlay{
  position:fixed;
  top:0;
  bottom:0;
  left:0;
  right:0;
  background:black;
  visibility: hidden;
  opacity: 0;
}
.overlay:target{
  visibility: visible;
  opacity: 1;
}
.popup{
  margin: 70px auto;
  padding: 10%;
  background: cornflowerblue;
  width: 30%;
  position: relative;
}
.popup h4{
  margin-top:0;
  color: black;
  font-family: 'Lucida Handwriting';
}
.popup .close{
  position:absolute;
  top:20px;
  right:30px;
  font-size: 30px;
  font-weight: bold;
  color: black;
}
.popup .close:hover{
  color: rgb(155, 185, 240);
}
.popup .content{
  max-height: 30%;
  overflow: auto;
}
.boxb{
  color: rgb(155, 185, 240);
  font-family: none;
}
@media screen and (max-width: 700px){
  #nav{
      width:fit-content;
  }
  #rektangel{
      width: fit-content;
  }
  .rektangel3{
      width:fit-content;
  }
  .box{
      width: 70%;
  }
  .popup{
      width: 70%;
  }
}
@media screen and(max-width: 540px){
  #nav{
      width:fit-content;
  }
  #rektangel{
      width: fit-content;
  }
  .rektangel3{
      width:fit-content;
  }
  .box{
      width: 70%;
  }
  .popup{
      width: 70%;
  }
}

